import React from 'react';
import Button from '@material-ui/core/Button';
import Modal from '@material-ui/core/Modal';
import Typography from '@material-ui/core/Typography';
import firebase from 'firebase';
import 'firebase/firestore';
import { firestore } from './firebase';

const scanBtn = {
  top: '0px',
  right: '0px',
  bottom: '0px',
  left: '0px',
  margin: '30px auto',
  height:'100px',
  fontSize:'30px'
  };

export default class Battle extends React.Component {

  constructor(){
    super();
    this.state = {
      battle:false,
      winner:null,
      modal:false
    };
    this.compare=this.compare.bind(this);
    this.handleOpen=this.handleOpen.bind(this);
    this.handleClose=this.handleClose.bind(this);
    this.resetScan=this.resetScan.bind(this);
  }

  propTypes: {
  }

  compare() {

    const result1 = this.props.result1[0].codeResult.code;
    const result2 = this.props.result2[0].codeResult.code;
    const total1 = this.props.total1;
    const total2 = this.props.total2;
    const name1 = this.props.name1;
    const name2 = this.props.name2;

    console.log("result1:"+result1+" result2:"+result2);
    console.log(name1 + ":" + total1);
    console.log(name2 + ":" + total2);

    let winner = null;
    let winnerCode = null;

    if(total1-total2 < 0){
      winner = name2;
      winnerCode = result2;
    }else if(total1-total2 == 0){
      winner = "TIE";
    }else{
      winner = name1;
      winnerCode = result1;
    }

    this.setState({
        battle: true,
        winner: winner
      });

    console.log("looking for code "+winnerCode);

    if(winnerCode){
      firestore.collection('barcodes').doc(winnerCode)
      .get()
      .then(doc=>{
        console.log(doc.id,':',doc.data().wins);
        let winsTemp = 0;
        if(doc.data().wins==null) winsTemp=0;
        else winsTemp = doc.data().wins;

        console.log("winsTemp:"+winsTemp);

        //register to firestore
        firestore.collection("barcodes").doc(winnerCode).update({
            wins:winsTemp+1
          });
        });
    }

    this.handleOpen();
  };

  handleOpen = () => {
    this.setState({ modal: true });
  };

  handleClose = () => {
    this.setState({ modal: false });
    this.props.resetScan();
  };

  componentWillUnmount(){
    this.setState({
      battle: false,
      winner: null
    });
  }

  resetScan(){
    this.props.resetScan();
  }

  render() {

    const modalStyle = {
        position: 'absolute',
        width: '60%',
        backgroundColor: 'white',
        padding: '30px',
        transform: 'translate(20%, 40%)'
    };

    return (
      <div>
        {!this.state.battle ? <Button variant='contained' color='secondary' onClick={this.compare} style={scanBtn} fullWidth='true'>BATTLE</Button> : null}
        <Modal
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          open={this.state.modal}
          onClose={this.handleClose}
        >
          <div style={modalStyle}>
            <Typography variant="h4" id="modal-title">
              WINNER
            </Typography>
            <Typography variant="subtitle1" id="simple-modal-description">
              {this.state.winner}
              {this.state.battle ? <Button variant='contained' color='secondary' onClick={this.resetScan} size='small'>NEXT BATTLE</Button> : null}
            </Typography>
          </div>
        </Modal>
      </div>
    );
  }
};
